.aside{
    position: fixed;
    left: 0;
    top:0;
    background: var(--body-color);
    border-right: 1px solid rgba(0,0,0,0.05);
    padding: 2.5rem;
    width:120px;
    min-height: 60vh;
    display:flex;
    flex-direction:column ;
    justify-content: space-between;
    z-index: 10;

}
.nav__logo {
    text-align: left;
    size-adjust: 100px;
}

.nav__list{
display:flex;
flex-direction: column;
grid-row-gap: 1.5rem;
row-gap: 1.5rem;
}
.nav__link{
    font-size: 1.5rem;
    color:var(--title-color);
    font-weight: var(--font-bold);
    transition: .3s;
}

.nav__link:hover {
    color:hsl(43, 100%, 68%);
}
.home{
    position: relative;
    min-height: 100vh;
    display:flex;
    align-items: center;
    justify-content: center;
}

.intro{
    max-width: 340px;
    text-align: center;
}

.home__img{
    margin-bottom: .1rem;

}

.home__name{
    font-size: var(--h1-font-size);
    font-weight: var(--font-bold);
    margin-bottom: 0.5rem;
}

.home__socials{
    justify-content: center;
    display: flex;
    grid-column-gap: 1.75rem;
    -webkit-column-gap: 1.75rem;
            column-gap: 1.75rem;
    margin: 1.5rem 0;
    
}

.home__socials-link{
    color:var(--title-color);
    font-size: 1.3rem;
    transition: .3s;

}

.home__social-link:hover {
   color: hsl(43, 100%, 68%)
    

}

.scroll__down {
    position: absolute;
    bottom: 2.5rem;
    left:0;
    width: 100%;
}

.home__scroll-name {
    font-size: var(--small-font-size);
}

.mouse{
    border: 2px solid #454360;
    display:block;
    height:1.6rem;
    width: 1.25rem;
    margin: auto;
    margin-top: .75rem;
    border-radius: 1rem;
    position: relative;
}

@-webkit-keyframes ani-mouse {
    0% {
        top: 29%;

    }
    15% {
        top: 50%;
        
    }
    50% {
        top: 50%;
        
    }
    100% {
        top: 29%;
        
    }
}

@keyframes ani-mouse {
    0% {
        top: 29%;

    }
    15% {
        top: 50%;
        
    }
    50% {
        top: 50%;
        
    }
    100% {
        top: 29%;
        
    }
}

.wheel{
    background-color: var(--title-color);
    border-radius: 100%;
    width: 0.25rem;
    height: 0.25rem;
    position: absolute;
    top: .5rem;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    -webkit-animation: ani-mouse 2s linear infinite;
            animation: ani-mouse 2s linear infinite;
}

