.about__container {
    grid-template-columns: 3fr 9fr;
    grid-column-gap: 1.85rem;
    -webkit-column-gap: 1.85rem;
            column-gap: 1.85rem;   
}

.about__data {
    padding: 1.875rem;
    background-color: var(--container-color);
    box-shadow: var(--shadow);
    border-radius: var(--border-radius);
    grid-template-columns: repeat(2 ,1fr);
    grid-column-gap: 1.875rem;
    -webkit-column-gap: 1.875rem;
            column-gap: 1.875rem;
    align-items: flex-start;
    position:relative; 

}

.about__data::before {
    content: '';
    width:0;
    height:0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-right: 15px solid var(--container-color);
    position: absolute;
    left: -0.93rem;
    top: 20%;
}
.about__description {
    margin-bottom: 1rem;
    text-align: justify;
}
.about__skills { 
  grid-row-gap: 1.25rem; 
  row-gap: 1.25rem;
} 
.skill__titles {
 display: flex;
 justify-content: space-between;
}

.skills__name {
 font-size: var(--normal-font-size);
 -webkit-text-decoration: 2px solid underline;
         text-decoration: 2px solid underline;
}

.skill__number {
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    display: flex;
       
}

.about__skills {
    display: flex; 
    justify-content: space-around;
    color: #183154;
    font-size: 2.5rem;
    font-weight: 200;
    height: 2rem;
     
}

.about__html:hover {
    color: red;  
}
.about__css:hover {
    color: #176ebe;  
}

.about__js:hover {
    color: #f9d032;
}

.about__bootstrap:hover {
    color: #7952b3;
}

.about__react:hover {
    color: #5ed3f3; 
}
.about__node:hover {
    color: #72b158;
}

.about__git:hover {
    color: black;
}

