* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  min-height: 100vh;
  background: #232427;
}

body .container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 40px 0;
}

body .container .card {
  position: relative;
  min-width: 320px;
  height: 440px;
  box-shadow: inset 5px 5px 5px rgba(0, 0, 0, 0.2),
    inset -5px -5px 15px rgba(255, 255, 255, 0.1),
    5px 5px 15px rgba(0, 0, 0, 0.3), -5px -5px 15px rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  margin: 30px;
  transition: 0.5s;
}

body .container .card:nth-child(1) .box .content a {
  background: #2196f3;
}

body .container .card:nth-child(2) .box .content a {
  background: #e91e63;
}

body .container .card:nth-child(3) .box .content a {
  background: #23c186;
}

body .container .card .box {
  position: absolute;
  top: 20px;
  left: 20px;
  right: 20px;
  bottom: 20px;
  background: #2a2b2f;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  transition: 0.5s;
}

body .container .card .box:hover {
  -webkit-transform: translateY(-50px);
          transform: translateY(-50px);
}

body .container .card .box:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  /* background: rgba(255, 255, 255, 0.03); */
}

body .container .card .box .content {
  padding: 20px;
  text-align: center;
}

body .container .card .box .content h2 {
  position: absolute;
  top: -10px;
  right: 30px;
  font-size: 8rem;
  color: rgba(255, 255, 255, 0.1);
}

body .container .card .box .content h3 {
  font-size: 1.8rem;
  color: #fff;
  z-index: 1;
  transition: 0.5s;
  margin-bottom: 15px;
}

body .container .card .box .content p {
  font-size: 1rem;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.9);
  z-index: 1;
  transition: 0.5s;
}

body .container .card .box .content a {
  position: relative;
  display: inline-block;
  padding: 8px 20px;
  border-radius: 5px;
  text-decoration: none;
  color: white;
  margin-top: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}
body .container .card .box .content a:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.6);
  background: #fff;
  color: #000;
}


.contact__container{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Rubik', sans-serif;
}

.contact__container .contactInfo {
    width: 60%;
    display: flex;
    flex-direction: column;
    font-family: 'Rubik', sans-serif;
}

.contact__container .contactInfo .box {
   position: relative;
   padding: 20px 0;
   display: flex;
   font-family: 'Rubik', sans-serif;
}
 
.contact__container .contactInfo .box  .icon {
    min-width: 60px;
    height: 60px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-size: 23px;
    font-family: 'Rubik', sans-serif;

 }
 .contact__container .contactInfo .box .text {
    display: flex;
    margin-left: 20px;
    font-size: 16px;
    color: black;
    flex-direction: column;
    font-weight: 300;
    font-family: 'Rubik', sans-serif;
 }

 .contact__container .contactInfo .box .text h3 {
    font-weight: 500;
    color: #333;
    font-family: 'Rubik', sans-serif;
 }
.contactForm {
    width: 50%;
    padding: 40px;
    background: #fff;
    font-family: 'Rubik', sans-serif;

}
.contactForm h2 {
    font-size: 30px;
    color:#333;
    font-weight: 500;
    font-family: 'Rubik', sans-serif;
}

.contactForm .inputBox {
    position:relative;
    width: 100%;
    margin-top: 10px;
    font-family: 'Rubik', sans-serif;
}

.contactForm .inputBox input,textarea {
    width: 100%;
    padding: 5px 0;
     font-size: 16px;
     margin: 10px 0;
     border: none;
     border-bottom: 2px solid #333;
     outline: none;
     resize: none;
     font-family: 'Rubik', sans-serif;

}
.contactForm .inputBox span {
    position: absolute;
    left:0;
    padding: 5px 0;
    font-size: 16px;
    margin: 10px 0;
    pointer-events: none;
    -webkit-transform: 0.5s;
            transform: 0.5s;
    color: #666;
    font-family: 'Rubik', sans-serif;
}

.contactForm .inputBox input:focus ~ span,
.contactForm .inputBox input:valid ~ span,
.contactForm .inputBox textarea:focus ~ span,
.contactForm .inputBox textarea:valid ~ span {
    color: #e91e63;
    font-size: 12px;
    -webkit-transform:translateY(-20px);
            transform:translateY(-20px);
    font-family: 'Rubik', sans-serif;
}
.contactForm .inputBox [type="submit"] {
    width: 130px;
    background: #ED495F;
    color: #fff;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    padding: 10px;
    font-size: 18px;
    font-family: 'Rubik', sans-serif;
}

@media (max-width: 991px) {
    .contact {
      padding: 50px;
    }
    .container {
        flex-direction: column;
    }
    .container .contactInfo  {
        margin-bottom: 40px;
        
    }
    .container .contactInfo, 
    .contactForm {
        width: 100%;
    }
}








