/* ====================GOOGLE FONTS=======================*/

 
:root{
    /*=============colors=============*/
     --first-color: hsl(353 ,100% ,65% );
     --title-color: hsl(244, 24%,26%);
     --text-color: hsl(244, 16%, 43%);
     --body-color: hsl(258, 60%, 98%);
     --container-color: #fff;

     /*=============FONTS AND TOPOGRAPHY=============*/
     --body-font: 'Rubik', sans-serif;
     --h1-font-size: 2.25rem;
     --h2-font-size: 1.5rem;
     --h3-font-size: 1.25rem;
     --normal-font-size: 1rem;
     --small-font-size: 0.875rem;
     --smaller-font-size: 0.813rem;


   /*=============FONTS weight=============*/
      --font-medium: 500;
      --font-semibold: 600;
      --font-bold: 700;

   /*=============Box shadows=============*/

      --shadow: 0px 5px 20px 0px rgb(69 67 96 / 10%);

   /*=============Box Radius=============*/

      --border-radius:20px;

}
/* ==================== Responsive Topography =======================*/
@media screen and (max-width:1024px)
   { :root {
        --h1-font-size: 1.75rem;
        --h2-font-size: 1.25rem;
        --h3-font-size: 1rem;
        --normal-font-size: 0.938rem;
        --small-font-size: 0.813rem;
        --smaller-font-size: 0.75rem;

    }
   }
/* ==================== BASE =======================*/
*{
    border: 0;
    padding: 0;
    box-sizing:border-box;
}
html{
    scroll-behavior: smooth;
}
body,button,textarea,input{
    font-family: 'Rubik', sans-serif;
    font-family: var(--body-font);
    font-size: 1rem;
    font-size: var(--normal-font-size);
}
body{
    background-color: hsl(258, 60%, 98%);
    background-color: var(--body-color);
    list-style: 1.7;
    color:hsl(244, 16%, 43%);
    color:var(--text-color);
}
h1,h2,h3{
    color: hsl(244, 24%,26%);
    color: var(--title-color);
    font-weight: 600;
    font-weight: var(--font-semibold);
    line-height:1.2;
}
ul{
    list-style: none;
}
a{
    text-decoration: none;
}
button{
    cursor: pointer;
    border:none;
    outline:none;
}
img{
    max-width: 100%;
    height: auto;
    vertical-align: middle;

}
/* ==================== REUSABLE CSS CLASES =======================*/
.container{
    max-width: 1080px;
    padding-left: 15px;
    padding-right: 15px;
    margin:0 auto;

}
.grid{
    display:grid;

}
.section{
    padding-top: 7rem;
    padding-bottom: 2rem;

}
.section__title{
    font-size: 2.25rem;
    font-size: var(--h1-font-size);
    margin-left: 00.875rem;
    font-weight: 700;
    font-weight: var(--font-bold);
    position:relative;
    margin-bottom: 3.75rem;

}
.section__title::before{
    content:"";
    background: url(/static/media/dots-bg.aa8289c5.svg);
    height:2.25rem;
    width:2.25rem;
    position:absolute;
    left: -0.875rem;
    top: -0.875rem;
}
.btn{
    padding:0.75rem;
    line-height: 1;
    border-radius:1.875rem;
    box-shadow: 0 0 1px rgb(0 0 0 / 0%);
    border: 1px solid transparent;
    color:#fff;
    display:inline-block;
    background-color: hsl(353 ,100% ,65% );
    background-color: var(--first-color);
    font-weight: 700;
    font-weight: var(--font-bold);
}



@-webkit-keyframes buttonpush {
    50% {
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
    }
    100% {
        -webkit-transform:scale(1);
                transform:scale(1);
    }
}



@keyframes buttonpush {
    50% {
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
    }
    100% {
        -webkit-transform:scale(1);
                transform:scale(1);
    }
}
.btn:hover{
    -webkit-animation: button-push 0.3s linear 1;
            animation: button-push 0.3s linear 1;
}
